.questions {
	margin: 55px 0 82px;
	color: $blue;
	@include md {
		margin: 36px 0 50px; }
	@include sm {
		margin: 48px 0 36px; }
	&__wrap {
		padding: 80px 100px 124px 105px;
		background-color: $white;
		border-radius: 16px;
		@include md {
			padding: 40px 58px 50px 37px; }
		@include sm {
			padding: 25px 22px 35px 30px;
			border-radius: 8px; } }
	&__title-lg {
		margin-bottom: 45px;
		@include md {
			margin-bottom: 35px; }
		@include sm {
			margin-bottom: 30px; } } }
