.team {
	padding: 120px 0;
	color: $white;
	@include md {
		padding: 55px 0 90px; }
	@include sm {
		padding: 40px 0 0; }
	&__title {
		margin-bottom: 40px;
		@include md {
			margin-bottom: 25px; }
		@include sm {
			text-align: center; } }
	&__wrap {
		margin: 0 -41px;
		font-size: 0;
		@include md {
			margin: 0 -15px; } }
	&__col {
		margin-bottom: 60px;
		padding: 0 41px;
		width: 33.3%;
		vertical-align: top;
		display: inline-block;
		&:last-child {
			@include sm {
				margin-bottom: 0; } }
		@include md {
			margin-bottom: 30px;
			padding: 0 15px; }
		@include sm {
			width: 100%; } }
	&__item {
		text-align: center; }
	&__img {
		margin: 0 auto 20px;
		width: 269px;
		height: 269px;
		border-radius: 50%;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		@include sm {
			width: 202px;
			height: 202px; } }
	&__name {
		margin-bottom: 8px;
		@include f(30px,600,1.2);
		@include md {
			font-size: 24px; } }
	&__post {
		@include f(18px,400,1.2);
		@include md {
			font-size: 14px; } } }
