.roadmap {
	padding: 180px 0 185px;
	color: $white;
	@include md {
		padding: 152px 0 54px; }
	@include sm {
		padding: 63px 0 225px; }
	&__head {
		margin-bottom: 138px;
		@include md {
			margin-bottom: 111px; }
		@include sm {
			margin-bottom: 126px; } }
	&__title {
		line-height: 1;
		@include md {
			font-size: 54px; } }
	&__description {
		font-size: 48px;
		font-weight: 600;
		@include md {
			font-size: 36px;
			line-height: 1; } }
	&__wrap {
		position: relative; }
	&__out {
		@include sm {
			margin: 0 auto;
			max-width: 290px; } }
	&__row {
		@include clr;
		&:not(:first-child) {
			margin-top: 94px;
			@include sm {
				margin-top: 35px; }
			&:nth-child(2n-1) {
				@include sm {
					margin-top: 193px; } } } }
	&__col {
		padding-left: 17px;
		width: 50%;
		display: table;
		min-height: 280px;
		@include md {
			min-height: 186px; }
		@include sm {
			position: relative;
			float: right;
			padding: 0;
			width: 100%;
			min-height: 0;
			width: calc( 100% + ( ( 100vw - 100% ) / 2 - 30px / 2 ) ); }
		&:before {
			position: absolute;
			content: "";
			top: 0;
			right: 23px;
			bottom: -16px;
			width: 8px;
			display: none;
			background: url(../img/circle.png) repeat-y;
			background-size: contain;
			@include sm {
				display: block; }
			.roadmap__row:first-child & {
				bottom: -40px;
				background: $white; }
			.roadmap__row:nth-child(4n-2) &,
			.roadmap__row:nth-child(4n-3) & {
				left: 23px;
				right: auto; }
			.roadmap__row:nth-child(3) & {
				top: -12px;
				bottom: -20px; }
			.roadmap__row:nth-child(4) & {
				top: -13px;
				bottom: -13px; }
			.roadmap__row:nth-child(6) & {
				top: -16px;
				bottom: -38px; } }


		.roadmap__row:first-child & {
			@include sm {
				min-height: 200px; } }
		.roadmap__row:nth-child(2) & {
			@include sm {
				min-height: 134px; } }
		.roadmap__row:nth-child(3) & {
			@include sm {
				min-height: 134px; } }
		.roadmap__row:nth-child(4) & {
			@include sm {
				min-height: 200px; } }
		.roadmap__row:nth-child(5) & {
			@include sm {
				min-height: 163px; } }
		.roadmap__row:nth-child(6) & {
			@include sm {
				min-height: 141px; } }

		.roadmap__row:nth-child(odd) & {
			padding-left: 40px;
			float: right;
			@include sm {
				padding: 0; } }
		.roadmap__row:nth-child(4n-2) &,
		.roadmap__row:nth-child(4n-3) & {
			@include sm {
				float: left; } } }
	&__circle {
		display: none;
		@include sm {
			top: 0;
			right: 0;
			left: auto;
			margin-left: 0;
			display: block; }
		.roadmap__row:nth-child(4n-2) &,
		.roadmap__row:nth-child(4n-3) & {
			@include sm {
				left: 0; } }
		.roadmap__row:nth-of-type(5n+3) &,
		.roadmap__row:nth-of-type(6n+4) & {
			&:before {
				left: 0; } } }
	&__box {
		position: absolute;
		display: none;
		@include sm {
			display: block; }
		.roadmap__row:first-child & {
			left: 235px;
			top: -305px;
			width: 144px; }
		.roadmap__row:nth-of-type(3) & {
			right: 107px;
			top: -160px;
			width: 72px; }
		.roadmap__row:nth-of-type(5) & {
			left: 110px;
			top: -155px;
			width: 72px; }
		.roadmap__row:nth-of-type(6) & {
			left: 96px;
			top: 185px;
			width: 115px; }
		img {
			max-width: 100%;
			display: block; } }
	&__vert {
		position: relative;
		padding-left: 70px;
		vertical-align: top;
		@include f(18px,300,2);
		display: table-cell;
		@include md {
			padding-left: 50px;
			font-size: 14px;
			line-height: 1.6; }
		@include sm {
			padding: 0 70px 0 0; }
		.roadmap__row:nth-child(4n-2) &,
		.roadmap__row:nth-child(4n-3) & {
			@include sm {
				padding: 0 8px 0 75px; } } }
	&__line {
		position: absolute;
		width: 145px;
		display: none;
		@include sm {
			display: block; }
		.roadmap__row:first-child & {
			left: 22px;
			bottom: 100%;
			margin-bottom: -12px;
			width: 280px; }
		.roadmap__row:nth-child(2) & {
			top: 100%;
			left: 23px;
			margin-top: 8px;
			width: 108px; }
		.roadmap__row:nth-child(3) & {
			right: 23px;
			margin-bottom: 15px;
			bottom: 100%; }
		.roadmap__row:nth-child(4) & {
			top: 100%;
			right: 23px;
			margin-top: 0;
			width: 119px; }
		.roadmap__row:nth-child(5) & {
			left: 23px;
			bottom: 100%;
			width: 112px; }
		.roadmap__row:nth-child(6) & {
			left: 23px;
			top: 100%;
			margin-top: 35px;
			width: 280px; }
		img {
			max-width: 100%;
			display: block; } }
	&__text {
		max-width: 360px;
		@include sm {
			max-width: 100%; } }
	&__icon {
		position: absolute;
		top: 5px;
		left: 0;
		@include sm {
			display: none; }
		.icon {
			font-size: 48px;
			@include md {
				font-size: 36px; } }
		img {
			display: block; } }
	&__title-sm {
		margin-bottom: 6px;
		font-family: $montserrat;
		@include f(36px,600,1.1);
		@include md {
			font-size: 27px; }
		@include sm {
			margin-bottom: 10px; } } }
