.news {
	padding: 35px 0 55px;
	color: $white;
	@include md {
		padding: 35px 0; }
	@include sm {
		padding: 25px 0; }
	&__title {
		margin-bottom: 38px;
		@include md {
			margin-bottom: 21px; }
		@include sm {
			margin-bottom: 29px; } }
	&__col {
		&:not(:first-child) {
			@include sm {
				margin-top: 40px; } } }
	&__footer {
		margin-top: 30px;
		text-align: center;
		@include md {
			margin-top: 10px; }
		@include sm {
			margin-top: 40px; } } }
.item {
	&__img {
		margin-bottom: 20px;
		height: 300px;
		background-color: $itemBg;
		border-radius: 8px;
		background-position: 50% 50%;
		background-size: cover;
		background-repeat: no-repeat;
		@include md {
			margin-bottom: 26px;
			height: 216px; } }
	&__title-sm {
		margin-bottom: 10px;
		@include md {
			margin-bottom: 14px; } } }
