.article {
	color: $blue;
	@include f(18px, 300, 2);
	@include md {
		font-size: 14px;
		line-height: 1.6; }
	.title-lg {
		&:not(:last-child) {
			margin-bottom: 25px;
			@include md {
				margin-bottom: 10px; } } }
	p {
		&:not(:last-child) {
			margin-bottom: 30px;
			@include md {
				margin-bottom: 23px; } } } }
