.code {
	border: 8px solid $light-turquoise;
	border-radius: 8px;
	@include clr;
	@include md {
		border-width: 6px; }
	&__input {
		overflow: hidden;
		input {
			padding: 20px 35px;
			height: 80px;
			width: 100%;
			font-family: $montserrat;
			@include f(24px,700,1.2);
			white-space: nowrap;
			text-overflow: ellipsis;
			display: block;
			@include md {
				padding: 18px 25px;
				height: 60px;
				font-size: 18px; }
			@include sm {
				padding: 13px 15px;
				height: 48px;
				font-size: 14px; } } }
	&__btn {
		float: right;
		height: 80px;
		padding: 26px 26px 26px 34px;
		color: $white;
		font-family: $montserrat;
		@include f(24px,700,1.2);
		background-color: $light-turquoise;
		@include md {
			padding: 12px 16px 12px 22px;
			height: 60px; }
		@include sm {
			padding: 9px 10px 9px 16px;
			height: 48px; }
		.icon {
			font-size: 28px;
			vertical-align: middle; } } }
