.relations {
	position: relative;
	z-index: 1;
	padding: 82px 0 0;
	color: $white;
	@include md {
		padding: 58px 0 0; }
	@include sm {
		padding: 30px 0 0; }
	&__out {
		table-layout: fixed;
		width: 100%;
		display: table;
		@include sm {
			display: flex;
			flex-wrap: wrap; } }
	&__subscribe,
	&__channels {
		display: table-cell;
		@include sm {
			width: 100%;
			display: block; } }
	&__channels {
		padding-left: 20px;
		vertical-align: middle;
		@include md {
			padding: 29px 0 0 0;
			vertical-align: top; }
		@include sm {
			padding-top: 55px;
			order: 2; } }
	&__subscribe {
		width: 500px;
		vertical-align: top;
		@include md {
			width: 380px; }
		@include sm {
			width: 100%; } }
	&__channels-wrap {
		text-align: center;
		max-width: 370px;
		@include md {
			text-align: left;
			max-width: 80%;
			min-width: 264px; }
		@include sm {
			max-width: 100%; } }
	&__row {
		&:not(:first-child) {
			margin-top: 24px; } }
	&__btn {
		min-width: 100%;
		@include md {
			min-width: 100%; }
		@include sm {
			min-width: 100%; } }
	&__footer {
		margin-top: 30px;
		font-size: 18px;
		font-weight: 600;
		@include sm {
			text-align: center; } } }

