.out {
	position: relative;
	overflow: hidden; }

.inner,
.inner-sm {
	position: relative;
	z-index: 1;
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
	@include clr;
	@include md {
		padding: 0 38px; } }

.inner {
	@include md {
		padding: 0 38px; }
	@include sm {
		padding: 0 5px; } }

.inner-sm {
	max-width: 1025px;
	@include md {
		padding: 0 66px; }
	@include r(839) {
		padding: 0 38px; }
	@include sm {
		padding: 0 15px; } }
