.btn-menu {
  position: relative;
  float: right;
  margin-top: 10px;
  width: 16px;
  height: 14px;
  background-color: transparent;
  display: none;
}

@media only screen and (max-width: 767px) {
  .btn-menu {
    display: block;
  }
}

.btn-menu__line, .btn-menu:before, .btn-menu:after {
  position: absolute;
  content: "";
  left: 0;
  width: 16px;
  height: 2px;
  transition: transform .3s, opacity .3s;
  background-color: #fff;
}

.btn-menu:before {
  top: 0;
}

.is-menu-open .btn-menu:before {
  transform-origin: left top;
  transform: rotate(45deg) translate(2px, -1px);
}

.btn-menu:after {
  bottom: 0;
}

.is-menu-open .btn-menu:after {
  transform-origin: left bottom;
  transform: rotate(-45deg) translate(2px, 1px);
}

.btn-menu__line {
  top: 50%;
  margin-top: -1px;
}

.is-menu-open .btn-menu__line {
  opacity: 0;
  transform: scaleX(0);
}

