body {
	font-family: $montserrat;
	@include f(14px, 400, 1.6);
	min-width: 320px;
	@include font-smoothing;
	background-color: $turquoise; }

.row {
	margin: 0 -17px;
	@include clr;
	@include md {
		margin: 0 -12px; } }
.col {
	float: left;
	padding: 0 17px;
	@include md {
		padding: 0 12px; }
	&-33 {
		width: 33.3%; }
	&-sm-100 {
		@include sm {
			width: 100%; } } }

.light-turquoise {
	color: $light-turquoise; }

.wrap-white {
	padding: 65px 100px 107px 98px;
	background-color: $white;
	border-radius: 16px;
	box-shadow: 0 24px 72px rgba(29, 53, 78, 0.25);
	@include md {
		padding: 35px 40px 48px 37px; }
	@include sm {
		padding: 20px 25px 38px;
		border-radius: 8px; } }
