.tokens {
	&__code {
		margin-top: 40px;
		@include md {
			margin-top: 31px; } }
	&__buttons {
		margin-top: 48px;
		font-size: 0;
		@include md {
			margin-top: 36px; }
		@include r(840) {
			text-align: center; }
		@include sm {
			margin-top: 15px; }
		.btn {
			min-width: 370px;
			@include md {
				min-width: 332px; }
			@include r(840) {
				min-width: 290px; }
			@include sm {
				min-width: 0;
				width: 100%; }
			&:not(:last-child) {
				margin-right: 48px;
				@include md {
					margin-right: 18px; }
				@include sm {
					margin: 0 0 16px; } } } }
	&__title-middle {
		&:not(:last-child) {
			margin-bottom: 25px;
			@include sm {
				margin-bottom: 20px; } }
		@include sm {
			margin-right: -10px; } }
	&__title {
		@include sm {
			margin-right: -15px; } }
	&__info {
		font-size: 18px;
		font-weight: 300;
		line-height: 2;
		@include md {
			font-size: 14px;
			line-height: 1.6; }
		&:not(:last-child) {
			margin-top: 25px; } }
	&__img {
		&:not(:first-child) {
			margin-top: 20px; }
		img {
			max-width: 100%;
			display: block; } } }
